:root {
  --menu--transition: transform 0.195s cubic-bezier(0.694, 0.0482, 0.335, 1);
  --brand-color: #002347; }

label,
.form-label {
  line-height: 1rem;
  color: var(--brand-color);
  font-size: 1rem;
  font-weight: bold; }

select {
  color: #212121; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-weight: normal; }

input::placeholder,
textarea::placeholder {
  opacity: 0.5;
  color: inherit; }

[role='button'],
button {
  cursor: pointer; }

input,
.form-input {
  margin-bottom: 0;
  border: 1px solid var(--brand-color);
  border-radius: 2px;
  height: 2.25rem;
  line-height: 2.25rem;
  color: #212121;
  font-size: 16px; }
  input:focus,
  .form-input:focus {
    transition: border linear .2s, box-shadow linear .2s;
    outline-style: none;
    border: 1px solid var(--brand-color);
    box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.4), 0 0 8px rgba(248, 172, 30, 0.5); }

textarea,
.form-textarea {
  border: 1px solid var(--brand-color);
  border-radius: 2px;
  padding: .5rem;
  resize: vertical;
  line-height: 2.25rem;
  color: #212121;
  font-size: 16px; }
  textarea:focus,
  .form-textarea:focus {
    transition: border linear .2s, box-shadow linear .2s;
    outline-style: none;
    border: 1px solid var(--brand-color);
    box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.4), 0 0 8px rgba(248, 172, 30, 0.5); }

.appearance-none {
  appearance: none; }
